.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(148, 132, 132); */
  /* background-color: rgb(20, 20, 20); */
  background-color: transparent;
  height: 10vh;
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  /* background-color: red; */
}
.nav_black {
  background-color: rgb(20, 20, 20);
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-color: lightblue; */
}
.header_left ul {
  width: 140%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  cursor: pointer;
  /* border-color: lightblue; */
}
.header_right ul {
  align-items: center;
  width: 140%;
  display: flex;
  justify-content: space-around;
  list-style: none;
  cursor: pointer;
  /* border-color: lightgreen; */
}
.header_outer_container a {
  text-decoration: none;
  color: white;
}
@media screen and (max-width: 950px) {
  .header_links {
    display: none;
  }
}
